.firework {
  height: 100%;
  width: 100%;
  position: absolute;
  background: #fff;
}
.main {
  overflow: hidden;
}
.launcher {
  position: absolute;
  z-index: 20;
  left: 50%;
  bottom: 37px;
  width: 100%;
  text-align: center;
  -webkit-transform: translatex(-50%);
  transform: translatex(-50%);
}
.launcher_item {
  display: inline-block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-right: 7%;
}
@-webkit-keyframes launcher {
  0% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
  60% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  to {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
}

@keyframes launcher {
  0% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
  60% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
  to {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
}
.J-ball,
.J-spark {
  position: fixed;
}
.J-ball {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  bottom: 0;
  background-color: #fff;
  opacity: 1;
}
.J-spark {
  position: absolute;
  width: 5px;
  height: 5px;
}
.btn {
  height: 32px;
  width: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  outline: none;
}
.modal-mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.45);
}
.modal-mask-hidden {
  display: none;
}
